<template>
  <div class="field has-addons">
    <p class="control">
      <input 
        class="input"
        type="text"
        placeholder="Find a post"
        @input="filterTable" 
      >
    </p>
  </div>
</template>

<script>
import _debounce from "lodash.debounce";
export default {
  name: "TableSearch",
  props: {
    label: {
      type: String,
      default: "Szukaj"
    }
  },
  methods: {
    filterTable: _debounce(function({ target = null }) {
      if (target) this.$emit("input", target.value);
      this.$nextTick(() => this.$emit("reload-table"));
    }, 500)
  }
};
</script>
