<template>
  <div
   
    class="select"
    :class="{'is-multiple':isMultiple}"
  >
    <template v-if="isMultiple">
      <select 
        v-model="selected"
        multiple
        @change="change"
      >
        <option
          value=""
          disabled
        >
          Puste
        </option>
        <template v-if="hasValues">
          <template v-for="(selectOption, index) in values">
            <option 
              :key="index"
              :disabled="selectOption.hasOwnProperty('disabled')?selectOption.disabled:false"
              :value="selectOption[valueKey]"
            >
              {{ selectOption[titleKey] }}
            </option>
          </template>
        </template>
      </select>
    </template>
    <template v-else>
      <select 
        :value="value"
        :multiple="isMultiple"
        @change="change"
      >
        <option
          value=""
          disabled
        >
          Puste
        </option>
        <template v-if="hasValues">
          <template v-for="(selectOption, index) in values">
            <option 
              :key="index"
              :disabled="selectOption.hasOwnProperty('disabled')?selectOption.disabled:false"
              :value="selectOption[valueKey]"
            >
              {{ selectOption[titleKey] }}
            </option>
          </template>
        </template>
      </select>
    </template>
  </div>
</template>

<script>
export default {
  name: "FormGeneratorSelect",
  props: {
    value: [String, Number, Array],
    isMultiple: {
      type: Boolean,
      default: false
    },
    valueKey: {
      type: String,
      default: "value"
    },
    titleKey: {
      type: String,
      default: "label"
    },
    values: {
      type: Array,
      default() {
        return [];
      },
      required: true
    }
  },
  data() {
    return {
      selected: this.value
    };
  },
  computed: {
    hasValues() {
      return this.values.length > 0;
    }
  },
  methods: {
    change({ target }) {
      if (this.isMultiple) {
        this.$emit("input", this.selected);
      } else {
        this.$emit("input", target.value);
      }
    }
  }
};
</script>
