import Vue from "vue";
import AsyncComputed from "vue-async-computed";
import Toasted from "vue-toasted";
import swal from "sweetalert";
import VueSelectImage from "vue-select-image";
import VModal from "vue-js-modal";

import alertMixin from "@Mixins/alertMixin";
import postCategories from "./post-categories/Categories.vue";
import postCategory from "./post-categories/Category.vue";

import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
// pages
import vPage from "./pages/Page";
import vPages from "./pages/Pages";
Vue.use(VueSelectImage);
Vue.use(VModal);
Vue.use(Toasted, {
    theme: "outline",
    position: "bottom-right",
    iconPack: "fontawesome",
    duration: 3000
});
Vue.use(AsyncComputed);

Vue.mixin({
    mixins: [alertMixin],
    methods: {
        handleError(e) {
            /*
               .then(({ data }) => this.handleResponseMessage(data))
                .catch(this.handleError);
                 */
            console.error(e);
            const { status, data } = e.response;
            if (status === 500) {
                this.simpleError({ text: data });
            } else {
                this.simpleError();
            }
        },
        handleResponseMessage(message) {
            return message === "success" ? this.simpleSuccess() : new Error();
        },
        async swalSuccess(status, text) {
            if (status === 200) {
                await swal(text, {
                    icon: "success",
                    closeOnClickOutside: false
                });
                setTimeout(() => location.reload(), 1000);
            } else {
                throw new Error();
            }
        }
    }
});
new Vue({
    el: "#app",
    components: {
        postCategories,
        postCategory,
        vPage,
        vPages
    }
});
