<template>
  <div class="field">
    <template v-if="label">
      <label
        :for="`input-${inputKey}`"
        class="label"
      >
        {{ label }}
        <template v-if="required">
          <span
            class="has-text-danger"
            title="wymagane pole"
          >*</span>
        </template>
      </label>
    </template>
    <div class="control">
      <template v-if="type==='number'">
        <input
          :id="`input-${inputKey}`"
          :value="value"
          type="number"
          class="input"
          :class="classObj"
          :disabled="disabled"
          :min="min"
          @input="change"
          @blur="blur"
        >
      </template>
      <template v-else-if="type==='password'">
        <input
          :id="`input-${inputKey}`"
          type="password"
          :value="value"
          class="input"
          :class="classObj"
          :disabled="disabled"
          @input="change"
          @blur="blur"
        >
      </template>
      <template v-else>
        <input
          :id="`input-${inputKey}`"
          type="text"
          :value="value"
          class="input"
          :class="classObj"
          :disabled="disabled"
          :name="inputKey"
          @input="change"
          @blur="blur"
        >
      </template>
    </div>
    <p
      v-if="errors.length>0"
      class="help is-danger"
    >
      {{ errors[0] }}
    </p>
  </div>
</template>

<script>
import { Validator } from "@cesium133/forgjs";
import _debounce from "lodash.debounce";
import EventBus from "@Custom/EventBus";
export default {
  name: "FormGeneratorInput",
  props: {
    value: {
      type: [String, Number],
      default: null
    },
    validationSchema: {
      type: Object,
      default: () => ({}),
      required: true
    },
    inputKey: {
      type: [String, Number],
      default: null,
      required: true
    },
    disabled: {
      default: false,
      type: Boolean
    },
    label: {
      type: String,
      default: null
    },
    placeholder: {
      type: String,
      default: ""
    },
    type: {
      default: "text",
      type: String
    },
    min: {
      type: Number,
      default: 0
    },
    required: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isTyping: false,
      passValidation: false,
      errors: []
    };
  },
  computed: {
    classObj() {
      const { isTyping, passValidation } = this;
      return {
        "is-success": isTyping && passValidation,
        "is-danger": isTyping && !passValidation
      };
    }
  },
  mounted() {
    EventBus.$on("trigger-validation", () => {
      const { test, errors } = this.validateInput(this.value);
      this.isTyping = true;
      this.passValidation = test;
      this.errors = errors;
    });

    EventBus.$on("default-state", () => {
      this.isTyping = false;
      this.passValidation = false;
    });
  },
  methods: {
    validateInput(value) {
      const { inputKey, type } = this;
      const trueValue = type === "number" ? parseFloat(value) : value;
      const vComplex = new Validator({
        [inputKey]: this.validationSchema[inputKey]
      });

      return {
        test: vComplex.test({
          [inputKey]: trueValue
        }),
        errors: vComplex.getErrors({ [inputKey]: trueValue })
      };
    },
    blur({ target }) {
      if (target) this.collectValue(target);
    },
    collectValue(target) {
      const { value } = target;
      const { test, errors } = this.validateInput(value);
      this.isTyping = true;
      this.$emit("input", value);

      this.passValidation = test;
      this.errors = errors;
    },
    change: _debounce(function({ target }) {
      this.collectValue(target);
    }, 500)
  }
};
</script>
