import { Rule } from "@cesium133/forgjs";
export const model = {
    title: "",
    seo_title: "",
    slug: "",
    seo_keywords: "",
    seo_description: "",
    status: false,
    content: ""
};

export const schema = [
    {
        model: "title",
        label: "Nazwa",
        required: true
    },
    {
        model: "seo_title",
        label: "SEO link",
        required: true
    },
    {
        model: "slug",
        label: "Slug",
        required: true
    },
    {
        model: "seo_keywords",
        label: "SEO keywords",
        required: true
    },
    {
        model: "seo_description",
        label: "Opis SEO",
        required: true
    }
];
const commonStringRule = {
    type: "string",
    minLength: 4,
    notEmpty: true
};
const commonUrlRule = {
    type: "url",
    notEmpty: true,
    protocol: prot => prot === "https" || prot === "http"
};
export const validationSchema = {
    title: new Rule(commonStringRule, "Minimum 4 symbole"),
    seo_title: new Rule(commonStringRule, "Minimum 4 symbole"),
    seo_keywords: new Rule(commonStringRule, "Minimum 4 symbole"),
    seo_description: new Rule(commonStringRule, "Minimum 4 symbole"),
    slug: new Rule(commonStringRule, "Minimum 4 symbole"),
    status: new Rule({ type: "boolean|number" }, null),
    content: new Rule(
        {
            type: "string",
            notEmpty: true
        },
        null
    )
};
