/**
 * ========= index.js ==========
 import Toasted from "vue-toasted";
 Vue.use(Toasted, {
 theme: "outline",
 position: "bottom-right",
 iconPack: "fontawesome",
 duration: 3000
});
========= component =========
import alertMixin from "@Mixins/alertMixin";
mixins: [alertMixin],
	
 */
const manual = icon => ({
    icon,
    duration: null,
    action: {
        text: "OK",
        onClick: (e, toastObject) => {
            toastObject.goAway(0);
        }
    }
});

const mixin = {
    methods: {
        simpleSuccess({
            text = "Success! Dane zostay zmienione",
            auto = true
        } = {}) {
            if (auto) {
                this.$toasted.success(text, {
                    icon: "thumbs-up",
                    duration: 5000
                });
            } else {
                this.$toasted.success(text, {
                    ...manual("thumbs-up")
                });
            }
        },
        simpleError({
            text = "Bd! Prosz sprbowa ponownie lub skontaktowa si z administratorem.",
            auto = true
        } = {}) {
            if (auto) {
                this.$toasted.error(text, {
                    icon: "exclamation",
                    duration: 5000
                });
            } else {
                this.$toasted.error(text, {
                    ...manual("exclamation")
                });
            }
            return false;
        },
        simpleInfo({ text = "Uwaga!", auto = true } = {}) {
            if (auto) {
                this.$toasted.info(text, {
                    icon: "exclamation",
                    duration: 5000
                });
            } else {
                this.$toasted.info(text, {
                    ...manual("exclamation")
                });
            }
            return false;
        }
    }
};
export default mixin;
