<template>
  <vue-tabs>
    <v-tab
      title="mainForm"
    >
      <div class="columns">
        <div class="column is-half">
          <div class="box">
            <template v-for="(item, index) in schema">
              <v-input
                :key="index"
                v-model="model[item.model]"
                :validation-schema="validationSchema"
                :label="item.label"
                :input-key="item.model"
                :type="item.type"
                :required="item.required"
              />
            </template> 
        
            <div class="field">
              <label class="checkbox">
                <input
                  v-model="model['status']"
                  type="checkbox"
                >
                Status
              </label>
            </div>

            <div class="field">
              <template v-if="isUpdate">
                <button
                  class="button is-medium is-primary"
                  @click="udpate"
                >
                  Update
                </button>
              </template>
              <template v-else>
                <button
                  class="button is-medium is-primary"
                  @click="create"
                >
                  Create
                </button>
              </template>
            </div>
          </div>
        </div>
      </div>
    </v-tab>
    <v-tab
      title="content"
    >
      <div class="columns">
        <div class="column">
          <div class="box">
            <quill-editor
              ref="myQuillEditor"
              v-model="model['content']"
              :options="editorOption"
              @blur="onEditorBlur($event)"
              @focus="onEditorFocus($event)"
              @ready="onEditorReady($event)"
            />
          </div>
        </div>
      </div>
    </v-tab>
  </vue-tabs>
</template>
<script>
import { Validator } from "@cesium133/forgjs";
import Axios from "axios";
import { VueTabs, VTab } from "vue-nav-tabs";
import { quillEditor } from "vue-quill-editor";
import Quill from "quill";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";

import vSelect from "@Custom/Select";
import EventBus from "@Custom/EventBus";
import vInput from "@Custom/Input";
import componentMixin from "@Mixins/componentMixin";
import { model, schema, validationSchema } from "./staticData";
export default {
  name: "SinglePage",
  components: {
    vInput,
    quillEditor,
    VueTabs,
    VTab
  },
  mixins: [componentMixin],
  data() {
    return {
      api: {
        admin: "/dashboard/pages"
      },
      model,
      schema,
      validationSchema,
      editorOption: {
        /* quill options */
      }
    };
  },
  computed: {
    validator() {
      const vComplex = new Validator(validationSchema);
      console.log(vComplex.getErrors(this.model));
      return vComplex.test(this.model);
    },
    pageId() {
      const { params } = this;
      return params ? params.id : null;
    },
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  methods: {
    onEditorBlur(quill) {
      console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log("editor change!", quill, html, text);
      this.content = html;
    },
    async create() {
      // if (!this.validator) {
      //   EventBus.$emit("trigger-validation");
      //   return alert("fill all required fields");
      // }
      try {
        const { status } = await Axios.post(this.api[this.role], this.model);
        this.swalSuccess(status, "Category is successfully created!");
      } catch (error) {
        this.handleError(error);
      }
    },
    udpate() {
      // if (!this.validator) {
      //   EventBus.$emit("trigger-validation");
      //   return alert("fill all required fields");
      // }
      Axios.put(`${this.api[this.role]}/${this.pageId}`, this.model)
        .then(({ data }) => this.handleResponseMessage(data))
        .catch(this.handleError);
    }
  }
};
</script>
<style>
.ql-editor {
  min-height: 50vh;
}
</style>
