<template>
  <div class="section">
    <!-- Main container -->
    <nav class="level">
      <!-- Left side -->
      <div class="level-left">
        <div class="level-item">
          <table-search
                  v-model="moreParams.search"
                  @reload-table="reloadTable"
          />
        </div>
      </div>

      <!-- Right side -->
      <div class="level-right">
        <p class="level-item">
          <strong>All</strong>
        </p>
        <p class="level-item">
          <a>Published</a>
        </p>
        <p class="level-item">
          <a>Drafts</a>
        </p>
        <p class="level-item">
          <a>Deleted</a>
        </p>
        <p class="level-item">
          <a
                  :href="createUrl"
                  class="button is-success"
          >New</a>
        </p>
      </div>
    </nav>
    <div class="container">
      <vuetable
              ref="vuetable"
              :api-url="listUrl"
              :fields="['id', 'name', 'description','options']"
              data-path="data"
              :per-page="10"
              :append-params="moreParams"
              pagination-path=""
              @vuetable:pagination-data="onPaginationData"
      >
        <div
                slot="options"
                slot-scope="props"
        >
          <button
                  class="button is-small is-danger"
                  @click="deleteProducer(props.rowData.id)"
          >
            Delete
          </button>
          <a
                  class="button is-small is-primary"
                  :href="`${editUrl}/${props.rowData.id}/edit`"
          >
            Edit
          </a>
        </div>
      </vuetable>
      <div class="pagination ui basic segment grid">
        <vuetable-pagination-info
                ref="paginationInfo"
        />

        <vuetable-pagination
                ref="pagination"
                @vuetable-pagination:change-page="onChangePage"
        />
      </div>
    </div>
  </div>
</template>


<script>
    import Axios from "axios";
    import tableSearch from "@Custom/TableSearch";
    import tableMixin from "@Mixins/tableMixin.js";
    export default {
        name: "PostCategories",
        components: { tableSearch },
        mixins: [tableMixin],
        data() {
            return {
                api: {
                    admin: {
                        list: "/dashboard/post_categories/list",
                        delete: "/dashboard/post_categories",
                        create: "/dashboard/post_categories/create",
                        edit: "/dashboard/post_categories"
                    }
                }
            };
        },

        methods: {
            deleteProducer(producerId) {
                let confirm = window.confirm(`Delete Product #${producerId}`);
                if (confirm) {
                    Axios.delete(`${this.deleteUrl}/${producerId}`).then(({ data }) => {
                        if (data === "success") {
                            alert(data);
                            this.reloadTable();
                        }
                    });
                }
            }
        }
    };
</script>
<style>
  .pagination {
    margin-top: 1rem;
  }
</style>
