<template>
  <div class="columns">
    <div class="column is-half">
      <div class="box">
        <template v-for="(item, index) in schema">
          <v-input
            :key="index"
            v-model="model[item.model]"
            :validation-schema="validationSchema"
            :label="item.label"
            :input-key="item.model"
            :type="item.type"
            :required="item.required"
          />
        </template> 
        
        <!-- CATEGORIES -->
        <div class="field">
          <label
            class="label"
            for="catogory"
          >Categories</label>
          <v-select
            v-model.number="model['parent_id']"
            :values="categories"
            title-key="name"
            value-key="id"
          />
        </div>
       
        <div class="field">
          <template v-if="isUpdate">
            <button
              class="button is-medium is-primary"
              @click="udpate"
            >
              Update
            </button>
          </template>
          <template v-else>
            <button
              class="button is-medium is-primary"
              @click="create"
            >
              Create
            </button>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Validator } from "@cesium133/forgjs";
import Axios from "axios";

import EventBus from "@Custom/EventBus";
import vInput from "@Custom/Input";
import vSelect from "@Custom/Select";
import componentMixin from "@Mixins/componentMixin";
import { model, schema, validationSchema } from "./staticData";
export default {
  name: "PostCategory",
  components: { vInput, vSelect },
  mixins: [componentMixin],
  props: {
    categories: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  data() {
    return {
      api: {
        admin: "/dashboard/post_categories"
      },
      model,
      schema,
      validationSchema
    };
  },
  computed: {
    validator() {
      const vComplex = new Validator(validationSchema);
      return vComplex.test(this.model);
    },
    categoryId() {
      return this.params ? this.params.id : null;
    }
  },
  // mounted() {
  //   const { selectedFilters } = this;
  //   if (this.isUpdate && selectedFilters.length > 0) {
  //     this.$set(this.model, "filters", selectedFilters);
  //   }
  // },
  methods: {
    async create() {
      // if (!this.validator) {
      //   EventBus.$emit("trigger-validation");
      //   return alert("fill all required fields");
      // }

      try {
        const { status } = await Axios.post(this.api[this.role], this.model);
        this.swalSuccess(status, "Category is successfully created!");
      } catch (error) {
        this.handleError(error);
      }
    },
    udpate() {
      // if (!this.validator) {
      //   EventBus.$emit("trigger-validation");
      //   return alert("fill all required fields");
      // }
      Axios.put(`${this.api[this.role]}/${this.categoryId}`, this.model)
        .then(({ data }) => this.handleResponseMessage(data))
        .catch(this.handleError);
    }
  }
};
</script>
