import Vuetable from "vuetable-2";
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import VuetablePaginationInfo from "vuetable-2/src/components/VuetablePaginationInfo";
import tableSearch from "@Custom/TableSearch";
export default {
    components: {
        Vuetable,
        VuetablePagination,
        VuetablePaginationInfo,
        tableSearch
    },
    computed: {
        vuetable() {
            return this.$refs.vuetable;
        },
        API() {
            const INSTANCE = this.$options.instance;
            return {
                list: `/dashboard/${INSTANCE}/list`,
                delete: `/dashboard/${INSTANCE}`,
                create: `/dashboard/${INSTANCE}/create`,
                edit: `/dashboard/${INSTANCE}`
            };
        }
    },
    data() {
        return {
            moreParams: {
                search: null
            }
        };
    },
    methods: {
        onPaginationData(paginationData) {
            this.$refs.pagination.setPaginationData(paginationData);
            this.$refs.paginationInfo.setPaginationData(paginationData);
        },
        onChangePage(page) {
            this.$refs.vuetable.changePage(page);
        },
        reloadTable() {
            this.$nextTick(() => {
                // this is required because vuetable uses tableFields internally, not fields
                this.vuetable.normalizeFields();
                this.vuetable.reload();
            });
        }
    }
};
