import { Rule } from "@cesium133/forgjs";
export const model = {
    name: "",
    description: "",
    parent_id: "",
    slug: ""
};

export const schema = [
    {
        model: "name",
        label: "Nazwa"
    },
    {
        model: "description",
        label: "Opis"
    },
    {
        model: "slug",
        label: "Slug"
    }
];
const commonStringRule = {
    type: "string",
    minLength: 4,
    notEmpty: true
};

export const validationSchema = {
    name: new Rule(commonStringRule, "Minimum 4 symbole"),
    description: new Rule(commonStringRule, "Minimum 4 symbole"),
    slug: new Rule(commonStringRule, "Minimum 4 symbole"),
    parent_id: new Rule(
        {
            type: "string|number"
        },
        null
    )
};
