export default {
    props: {
        params: {
            type: Object,
            default: null
        },
        isUpdate: {
            type: Boolean,
            default: false
        }
    },
    mounted() {
        if (this.isUpdate) {
            for (let [key, value] of Object.entries(this.params)) {
                if (Object.keys(this.model).includes(key) && value) {
                    this.$set(this.model, key, value || "");
                }
            }
        }
    }
};
